'use client';

import { useRouter } from 'next/navigation'
import Link from 'next/link';
import { prisma } from '@/lib/prisma';
import { StarIcon } from '@heroicons/react/20/solid';
import ReviewCard from '@/components/ReviewCard';

async function getRecentReviews() {
  const reviews = await prisma.review.findMany({
    take: 6,
    orderBy: {
      createdAt: 'desc',
    },
    include: {
      user: true,
      product: true,
      employee: true,
    },
  });
  return reviews;
}

function ReviewCard({ review }: { review: any }) {
    const title = review.product?.name || review.employee?.name || 'General Feedback';
    const type = review.product ? 'Product' : 'Employee';
  
    return (
      <div className="bg-white/80 backdrop-blur-sm rounded-xl shadow-lg p-6 border border-white/20">
        <div className="flex items-start justify-between mb-4">
          <div>
            <p className="font-bold text-lg text-gray-800">{title}</p>
            <p className="text-sm text-gray-500">by {review.user.name || 'Anonymous'}</p>
          </div>
          <span className={`px-2 py-1 text-xs font-semibold rounded-full ${
            type === 'Product' ? 'bg-purple-100 text-purple-800' : 'bg-orange-100 text-orange-800'
          }`}>
            {type} Review
          </span>
        </div>
        <p className="text-gray-700 italic mb-4">"{review.comment}"</p>
        <div className="flex items-center">
          {[...Array(5)].map((_, i) => (
            <StarIcon
              key={i}
              className={`h-5 w-5 ${i < review.rating ? 'text-yellow-400' : 'text-gray-300'}`}
            />
          ))}
        </div>
      </div>
    );
}

export default async function Home() {
  const reviews = await getRecentReviews();

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 via-pink-50 to-purple-50">
      <header className="py-4 px-8 flex justify-between items-center bg-white/50 backdrop-blur-md sticky top-0 z-10 shadow-sm">
        <h1 className="text-2xl font-bold bg-gradient-to-r from-orange-600 to-pink-600 bg-clip-text text-transparent">
          Dunkin' Feedback
        </h1>
        <Link href="/login" className="px-4 py-2 font-semibold text-white bg-pink-600 rounded-lg shadow-md hover:bg-pink-700 transition-colors">
          Login / Sign Up
        </Link>
      </header>

      <main className="py-16 px-4">
        <section className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-extrabold mb-4 bg-gradient-to-r from-orange-600 via-pink-600 to-purple-600 bg-clip-text text-transparent">
            Help Us Serve You Better
          </h2>
          <p className="text-lg text-gray-700 max-w-2xl mx-auto">
            Share your experience by rating our products and staff. Your feedback helps us improve.
          </p>
        </section>

        <section className="max-w-6xl mx-auto">
          <h3 className="text-3xl font-bold text-center mb-8 text-gray-800">Recent Feedback</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {reviews.map((review) => (
              <ReviewCard key={review.id} review={review} />
            ))}
          </div>
        </section>
        
        <section className="text-center mt-16">
            <h3 className="text-2xl font-bold mb-4 text-gray-800">Have something to share?</h3>
            <div className="flex justify-center gap-4">
                <Link href="/feedback/product" className="px-6 py-3 font-semibold text-white bg-purple-600 rounded-lg shadow-md hover:bg-purple-700 transition-colors">
                    Rate a Product
                </Link>
                <Link href="/feedback/employee" className="px-6 py-3 font-semibold text-white bg-orange-600 rounded-lg shadow-md hover:bg-orange-700 transition-colors">
                    Rate an Employee
                </Link>
            </div>
        </section>
      </main>

      <footer className="text-center py-6 border-t border-gray-200 mt-16">
        <p className="text-sm text-gray-600">
          &copy; {new Date().getFullYear()} Dunkin' Feedback |{' '}
          <Link href="/admin/login" className="hover:underline text-gray-700 font-medium">
            Admin Login
          </Link>
        </p>
      </footer>
    </div>
  )
}
